<template>
  <div id="layer">  
    <!--  -->  
    <div class="container" v-show="panel">  
      <div class="pic_con">  
        <!--   -->
        <img id="image" src="" alt="Picture">  
      </div>
      <div class="btns d_flex">
        <!-- <button type="button" class="btn_cancel" @click="crop"></button>
        <button type="button" class="btn_comfire" @click="crop"></button> -->
        <button type="button" class="btn_cancel" @click="cropCancel"></button>
        <button type="button" class="btn_comfire" @click="crop"></button>
      </div>
    </div>  
    
    <!--  -->
    <img :src="testUrl" id="cur_pic" v-show="false" alt="">

    <div class="origin_input">  
        <!-- <div class="show">  
          <div class="picture" :style="'backgroundImage:url('+headerImage+')'">  
          </div>  
        </div> -->

        <div>  
          <!--  -->
          <!-- <input type="file" id="change" accept="image" @change="change">  -->
          <!--  -->
          <input v-show="false" id="photoImg" type="file" accept="image/*" capture="camera" @change="change" >
          <!--  end -->
          <!--  -->
          <input v-show="false" id="gallery" type="file" accept="image/*" @change="change" >
          <!--  end --> 

          <label for="change"></label>  
        </div>  
    </div>  
    <!--  -->
    <mt-spinner color="rgb(255, 255, 255)" v-show="isUploading" class="uploading" type="fading-circle"></mt-spinner>
    <!--  end -->
  </div> 
</template>

  <script>
    import Cropper from 'cropperjs';
    import { Button, Toast, Spinner } from 'mint-ui';  
    export default {  
      components: {  

      },  
      data () {  
        return {  
          flag: false,
          headerImage:'',  
          picValue:'',  
          cropper:'',  
          croppable:false,  
          panel:false,//  
          url:'' ,
          testUrl: '', 
          photoImgFiles: '',//
          isUploading: false,//
        }  
      }, 
      components: {
        'mt-button': Button,
        'mt-spinner': Spinner,
        Toast,
      },
      mounted () {  
        //  
        var self = this;   
      },  
      methods: {  
        change (e){
          const _self = this;
          var file = e.target.files[0] || e.dataTransfer.files;
          var reader = new FileReader();
          var result = '';

          if( file.type !== "image/jpeg" ){
            console.log('')
            //   input
            e.target.value = null;
            _self.$options.methods.showToast('jpg');
            return
          }
          if (!file){
              return;
          }

          reader.readAsDataURL(file);//base64
          reader.onload = function() {
            // img.src = this.result;
            result = this.result; //
            var image = document.getElementById('image');

            // image.cropper('replace', result,false);
            _self.cropper = new Cropper(image, {
              aspectRatio: 1/1,//
              // preview: '.previewImg',//
              guides: false,  //()
              autoCropArea: 1,  //0-10.8
              movable: true, //
              // dragCrop: true,  //
              cropBoxMovable: true,  //
              cropBoxResizable: true,  //
              zoomable: true,  //
              mouseWheelZoom: false,  //
              touchDragZoom: true,  //
              rotatable: true,  //
              minContainerWidth: 300,
              minContainerHeight: 300,
              minCanvasWidth: 300,
              minCanvasHeight: 300,
              minCropBoxWidth: 300,
              minCropBoxHeight: 300,
              crop: function(){
                console.log('crop')
              },
              ready: function () {
                console.log('ready: ')
              }
            });

            _self.cropper.replace(result,false);
            _self.panel = true;//
          }
        }, 
        //
        crop (){
          if( this.flag ){return false}

          const _self = this;
          //
          var croppedCanvas = this.cropper.getCroppedCanvas();
          //base64
          var base64url = croppedCanvas.toDataURL('image/png'); 
          // console.log('dd: ', dd,base64url)
          this.testUrl = base64url;
          //
          var img = new Image,
          width = 120, //image resize   
          quality = 0.7, //image quality  
          canvascom = document.createElement("canvas"),
          drawer = canvascom.getContext("2d");
          img.src = base64url;
          // console.log()
          // return
          img.onload = function() {
            canvascom.width = width;
            canvascom.height = width * (img.height / img.width);

            drawer.drawImage(img, 0, 0, canvascom.width, canvascom.height);
            // var base64 = canvas.toDataURL("image/jpeg", quality); // base64

            var base64 = canvascom.toDataURL(img, quality); // base64
            _self.photoImgFiles = base64.split(',')[1];//
            
            var formData = new FormData();
            let config = {
              headers: {
                  'Content-Type': 'multipart/form-data'
                },
              transformRequest: [function (data) {
                return data
              }],
            }
            formData.append('memberID', _self.$store.state.memberId);
            formData.append('file',_self.photoImgFiles);//

            _self.isUploading = true;//
            _self.$ajax.post(_self.webRoot + '/updateByMemberId', formData, config)
            .then(function(res){
              // console.log(' ',res)
              if( res.data.success ){
                _self.photoSrc = base64;//
                _self.$options.methods.showToast('');
                // 
                _self.$options.methods.getMemberInfo(_self);
                _self.isUploading = false;
              }else{
                _self.$options.methods.showToast('');
                _self.isUploading = false;
              }
              _self.cropper.destroy();
              _self.panel = false;
              _self.flag = false;
            })
            .catch(function(err){
              console.log(err)
              _self.panel = false;
              _self.flag = false;
            });
          }
        },
        //
        cropCancel (){
          this.panel = false;
        },

        //
        // 
        getMemberInfo ( _self ){
          _self.$ajax({
            url: _self.webRoot + '/vipSearchByID',
            data: {
              id: _self.$store.state.memberId,
            }
          })
          .then(function(res){
            // console.log('', res)
            if( !res.data ){
              console.log('')
            }
            // 
            _self.$store.commit('add', {
              addMemberInfo: res.data
            });
            _self.$emit('updateInfo',res.data);
          })
          .catch(function(err){
            console.log(err)
          });
        },
        //
        showToast( msg, time ){
          var time = time ? time : 2000;

          Toast({
            message: msg,
            position: 'center',
            duration: time
          });     
        },
      }  
    }
  </script>

<style>
  @import 'cropperjs/dist/cropper.min.css';
  #layer .uploading>div {
    margin:0 auto;
  }
</style>

<style scoped>
    @import './Css/simple_cropper.css'
</style>


