<template>	
<section class="wrap has_header_wrap c_wrap">
	<!--  -->
	<com-header titles="">
		<template  slot="style-def"></template>
	</com-header>
	<!--  end -->
	
	<div class="personal_con">
		<!--  -->
		<div class="photo_block" @click.capture="changePhoto">
		<!-- <div class="photo_block"> -->
			<mt-cell class="photo" title="" is-link>
				<span>
					<!-- <img v-if="!personalInfo.picURL" src="../../../static/images/ak.png"alt=""> -->
					<img v-lazy="photoSrc" >
				</span>
				<mt-actionsheet
				  :actions="actions"
				  v-model="sheetVisible"
				  closeOnClickModal='false'>
				</mt-actionsheet>
			</mt-cell>
		</div>
		<!--  end -->

		<!--  -->
		<mt-cell title="" :value="personalInfo.memberCode" is-link></mt-cell>
		<!--  end -->

		<!--  -->
		<div @click="changeName">
			<mt-cell title="" :value="personalInfo.memberName" is-link></mt-cell>
		</div>
		<!--  end -->

		<!--  -->
		<div @click.capture="changeSex">
			<mt-cell title="" :value="personalInfo.sex" is-link>
				<span>{{ sexData }}</span>
				<mt-actionsheet
				  :actions="sex"
				  v-model="sexLayer"
				  closeOnClickModal='false'>
				</mt-actionsheet>
			</mt-cell>
		</div>
		<!--  end -->

		<!--  -->
		<div class="bornBlock" @click.capture="changeData">
			<mt-cell title="" :value="personalInfo.birthday" is-link>
			  <span>{{ bornDate }}</span>
			  <mt-datetime-picker
				  v-model="pickerVisible"
				  type="date" 
				  ref="picker"
				  year-format="{value} "
				  month-format="{value} "
				  date-format="{value} "
				  :startDate="startDate"
				  :endDate="endDate"
				  @confirm="confirmDate">
				</mt-datetime-picker>
			</mt-cell>
		</div>
		<!--  end -->
		
		<!--  -->
		<!-- <simple-cropper :initParam="uploadParam" :successCallback="uploadHandle" ref="cropper">  -->
		<simple-cropper v-on:updateInfo="updateInfoChild" ref="cropper">  
			<!-- <img :src="userImg" @click="upload">   -->
		</simple-cropper> 
		<!--  end -->


		<!--  -->
		<!-- <input v-show="false" id="photoImg" type="file" accept="image/*" capture="camera" @change="changePhotoImg" > -->
		<!--  end -->
		<!--  -->
		<!-- <input v-show="false" id="gallery" type="file" accept="image/*" @change="changePhotoImg" > -->
		<!--  end -->
	</div>		
      
  	<!--  -->
  	<com-foot></com-foot>
  	<!--  end -->

	<!--  -->
    <transition name="fade">
    	<router-view v-on:cName="getChangeName"></router-view>
    </transition>
    <!--  end -->

</section>
</template>

