			actions: [ // 
				{
					name: '',
					method: () => {
						// console.log(document.getElementById('change'))
						var ele = document.getElementById('photoImg');

						ele.click();
						// this.$emit('loadImg');
						// var ele = document.getElementById('change');
						// ele.click();
					}
				},
				{
					name: '',
					method: function(){
						// console.log('')
						var ele = document.getElementById('gallery');

						ele.click();
					}
				},
			],
			sexLayer: false, // 
			sex: [ //
				{
					name: '',
					method: () => {
						if( this.personalInfo.sex == '' ){
							return 
						}

						this.$options.methods.updateInfo( this, 'sex', '' );
						this.personalInfo.sex = '';
					}
				},
				{
					name: '',
					method: () => {
						if( this.personalInfo.sex == '' ){
							return 
						}

						this.$options.methods.updateInfo( this, 'sex', '' );
						this.personalInfo.sex = ''
					}
				}
			],
			// sexData: '' , // 
			pickerVisible: this.$store.state.memberInfo.birthday?this.$store.state.memberInfo.birthday:'', // 
			startDate: new Date('1970-01-01'), //
			endDate: new Date(), //
			// bornDate: '', //
			personalInfo: this.$store.state.memberInfo?this.$store.state.memberInfo:'',//
			photoSrc: this.$store.state.memberInfo?this.$store.state.memberInfo.picURL:'',//
			photoImgFiles: '',//
			//
			uploadParam: {  
		        fileType: 'recruit', //   
		        uploadURL: this.$dataURL + 'uploadAction/qcloudImg', //   
		        scale: 4  // : 4  
		    },  
		    userImg: this.$dataURL + 'test.png'
		}
	},
	components: {
		comHeaderTitle, // 
    	comFoot, // 
		'mt-cell': Cell,
		'mt-actionsheet': Actionsheet,
		'mt-datetime-picker': DatetimePicker,
		SimpleCropper  
	},
	filters: {
		
	},
	computed: {
		// 
		sexData (){
			if( this.personalInfo.sex ){
				return this.personalInfo.sex;
			}else{
				return '';
			}
		} , 
		//
		bornDate (){
			if( this.personalInfo.birthday ){
				return this.personalInfo.birthday;
			}else{
				return '';
			}
		}, 
	},
	created (){
		const _self = this;
	},
	mounted (){
		this.$emit('changeClass',false);
		// document.getElementById( 'loadImg').addEventListener( 'change', function () {
		//      var reader = new FileReader();
		//      reader.onload = function (e) {
		//           //compress();
		//      };
		//      // console.log(reader)
		//      reader.readAsDataURL(this.files[0]);
		//      console.log(this.files[0]);
		//      // var fileSize = Math.round( this.files[0].size/1024/1024) ; //M
		//      //this.files[0] byte sizethis.files[0].size;
		// }, false);
		// let URL = window.URL || window.webkitURL
		// console.log(URL);
	},
	updated (){
		
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//
		// changePhotoImg (e){
		// 	var _self = this;
		// 	var file = e.target.files[0] || e.dataTransfer.files;
		// 	var reader = new FileReader();
			
		// 	reader.readAsDataURL(file);//base64
		// 	reader.onload = function() {
	 //          img.src = this.result;
	 //        }
	 //        console.log(file)
	 //        var img = new Image,
			
		// 	width = 100, //image resize   
		// 	quality = 0.7, //image quality  
		// 	canvas = document.createElement("canvas"),
		// 	drawer = canvas.getContext("2d");

		// 	img.onload = function() {
	 //          canvas.width = width;
	 //          canvas.height = width * (img.height / img.width);
	 //          console.log(canvas.width, canvas.height)
	          
	 //          if( canvas.height < 100 ){
	 //          	alert('')
	 //          	e.target.value = null;
	 //          	file = '';
	 //          	return 
	 //          }
	 //          drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
	 //          // var base64 = canvas.toDataURL("image/jpeg", quality); // base64

	 //          var base64 = canvas.toDataURL(file.type, quality); // base64
	 //          _self.photoImgFiles = base64.split(',')[1];//
	 //          // console.log('base64',_self.photoImgFiles)
	 //          var formData = new FormData();
		// 	  let config = {
		// 		headers: {
		// 		    'Content-Type': 'multipart/form-data'
		// 			},
		// 		transformRequest: [function (data) {
		// 			return data
		// 		}],
		// 	   }
		// 	  formData.append('memberID', _self.$store.state.memberId);
	 //          formData.append('file',_self.photoImgFiles);//

	 //          _self.$ajax.post(_self.webRoot + '/updateByMemberId', formData, config)
	 //          .then(function(res){
	 //          	console.log(' ',res)
	 //          	if( res.data.success ){
	 //          		_self.photoSrc = base64;//
	 //          		_self.$options.methods.showToast('');
	 //          		// 
	 //  				_self.$options.methods.getMemberInfo(_self);
	 //  				console.log('dd:',_self.$store.state.memberInfo)
	 //          	}else{
	 //          		_self.$options.methods.showToast('');
	 //          	}
	 //          })
	 //          .catch(function(err){
	 //          	console.log(err)
	 //          });
	 //          //   input
	 //          e.target.value = null;
	 //          // console.log(_self.photoImgFiles)
	 //        }


		// },
		//
		changePhoto (){
			this.sheetVisible = true;
		},
		//
		changeName (){	
			this.$router.push({ path: 'info/infoset', query: { name: this.personalInfo.memberName }});
		},
		//
		getChangeName (data){
			// console.log(data)
			this.personalInfo.memberName = data;
			// this.$store.state.memberInfo
		},
		//
		changeSex (){
			this.sexLayer = true;
		},
		//
		changeData (){	
			this.$refs.picker.open();
		},
		//
		confirmDate (){
			// console.log(this.startDate.getFullYear())
			if( !this.pickerVisible ){
				var year = this.startDate.getFullYear();
				var month = this.startDate.getMonth()+1;
				if( month < 10 ){
					month = '0' + month;
				}
				var date = this.startDate.getDate();
				if( date < 10 ){
					date = '0' + date;
				}

				var bornDate = year + '-' + month + '-' + date;
				this.$options.methods.updateInfo( this, 'birthday', bornDate );
				this.personalInfo.birthday = bornDate;
				return false;
			}

			var year = this.pickerVisible.getFullYear();
			var month = this.pickerVisible.getMonth()+1;
			if( month < 10 ){
				month = '0' + month;
			}
			var date = this.pickerVisible.getDate();
			if( date < 10 ){
				date = '0' + date;
			}

			var bornDate = year + '-' + month + '-' + date;
			this.$options.methods.updateInfo( this, 'birthday', bornDate );
			this.personalInfo.birthday = bornDate;
		},
		//
		loadImg (){
			// console.log(6565656)
			// var reader = new FileReader(), img = new Image();
			// // 
			// reader.onload = function(e) {
			//   // e.target.resultbase64
			//   img.src = e.target.result;
			// };
			// console.log(img)
		},
		//   
	    upload () {  
	      this.$refs['cropper'].upload()  
	    },  
	    //   
	    uploadHandle (data) {  
	      if (data.state === 'SUCCESS') {  
	        this.userImg = this.form.headImgUrl = data.fileId  
	      }  
	    }, 
	    //
	    updateInfoChild ( data ){
	    	// console.log('updateInfoChild: ',data)
	    	this.personalInfo = data;
	    	//
	    	this.photoSrc = data.picURL;
	    	// console.log('aa: ', data.picURL, this.photoSrc)
	    	//
	    	this.$emit('updateInfo',data);
	    },

		//
	    //
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    //
		updateInfo ( _self, key, data ){
			var datas = {};

			datas.memberID = _self.$store.state.memberId;
			datas[key] = data;

			_self.$ajax({
				url: _self.webRoot + '/updateByMemberId',
				data: datas, 
			})
			.then(function(res){
				console.log(' ', res)
				if( !res.data.success ){
					_self.$options.methods.showToast( res.data.msg );
					return false;
				}
				_self.$store.state.memberInfo[key] = data;
				// 
  				_self.$store.commit('add', {
  					addMemberInfo: _self.$store.state.memberInfo,
  				});
				_self.$options.methods.showToast( '!', 1000 );
				return true;
			})
			.catch(function(err){
				console.log(err)
				return false;
			});
		},
		// 
		getMemberInfo ( _self ){
			_self.$ajax({
				url: _self.webRoot + '/vipSearchByID',
				data: {
					id: _self.$store.state.memberId,
				}
			})
			.then(function(res){
				// console.log(res)
				if( !res.data ){
					console.log('')
				}
				// 
  				_self.$store.commit('add', {
  					addMemberInfo: res.data
  				});
			})
			.catch(function(err){
				console.log(err)
			});
		},
	},
}	
</script>

<style scoped>
@import '../../../static/css/childre_wrap.css';
@import './Css/c_style.css';
</style>


